#footer {
    bottom: 0;
    width: 100%;
    height: 100px;
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0,0,128)), color-stop(rgba(0,0,200)), to(rgba(0,0,128)));
    background-image: -webkit-linear-gradient(rgba(0,0,128), rgba(0,0,200), rgba(0,0,128));
    background-image: -o-linear-gradient(rgba(0,0,128), rgba(0,0,200), rgba(0,0,128));
    background-image: linear-gradient(rgba(0,0,128), rgba(0,0,200), rgba(0,0,128));
}

a {
    color: white;
    font-family: 'Ubuntu', sans-serif;
    -webkit-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
}

a:hover {
    text-decoration: none;
    color: #e480d8;
}

#logo {
    height: 30px;
}

#name {
    font-size: 14px;
}

.footerWrapper {
    margin-top: 30px;   
}


